<template>
  <div class="card">
    <div class="card-body">
      <div class="row" v-if="invoicePage === false">
        <div class="col-xl-12 col-lg-12">
          <div>
            <a-tabs defaultActiveKey="1">
              <a-tab-pane tab="Invoices" key="1">
                <a-table
                  :columns="invoiceColumns"
                  :dataSource="usersData"
                  class="utils__scrollTable"
                  :scroll="{ x: '100%' }"
                >
                  <a slot="customer_name" slot-scope="text" href="javascript: void(0);">{{text.name}}</a>
                  <span slot="total_usd" slot-scope="text">${{text}}</span>
                  <span slot="total_ngn" slot-scope="text">₦{{text}}</span>
                  <span slot="action" slot-scope="text">
                    <span v-for="(textt, index) in text" :key="index">
                      <button v-if="textt.id === 'view'" @click="setInvoicePage(textt.url)" class="btn btn-info mr-2">{{textt.name}}</button>
                      <button v-if="textt.id === 'delete'" @click="deleteInvoice(textt.url)" class="btn btn-warning">{{textt.name}}</button>
                    </span>
                  </span>

                </a-table>
              </a-tab-pane>
            </a-tabs>
          </div>
        </div>
      </div>
      <cui-invoice :data="invoicePageData" v-if="invoicePage" @goBack="() => { invoicePage = false; invoicePageData = {} }" ></cui-invoice>
    </div>
  </div>
</template>

<script>
import {
  invoiceData,
} from './data.json'

import CuiInvoice from '../../components/CleanUIComponents/Invoice/index'

const invoiceColumns = [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
    sorter: (a, b) => a.id - b.id,
  },
  {
    title: 'Invoice Name',
    dataIndex: 'customer_name',
    key: 'customer_name',
    sorter: (a, b) => a.customer_name - b.customer_name,
    scopedSlots: { customRender: 'customer_name' },
  },
  {
    title: 'Total USD',
    dataIndex: 'total_usd',
    key: 'total_usd',
    sorter: (a, b) => a.total_usd - b.total_usd,
    scopedSlots: { customRender: 'total_usd' },
  },
  {
    title: 'Total Naira',
    dataIndex: 'total_ngn',
    key: 'total_ngn',
    sorter: (a, b) => a.total_ngn - b.total_ngn,
    scopedSlots: { customRender: 'total_ngn' },
  },
  {
    title: 'Actions',
    dataIndex: 'action',
    key: 'action',
    scopedSlots: { customRender: 'action' },
  },
]

export default {
  components: { CuiInvoice },
  data: function () {
    return {
      invoiceData,
      invoiceColumns,
      usersData: [],
      invoicePage: false,
      invoicePageData: [],
    }
  },
  mounted() {
    this.getInvoice()
  },
  methods: {
    setInvoicePage(id) {
      var data = this.usersData
      var query = id
      // search through the array and get the index array with the passed value
      var index = data.map(function (img) { return img.id }).indexOf(query)
      this.invoicePageData = this.usersData[index]
      this.invoicePage = true
    },
    deleteInvoice(id) {
      this.axios.get(`invoice/destroy/${id}`)
        .then(response => {
          // console.log(response)
          if (response.status === 200) {
            this.$notification['success']({
              message: 'Invoice Data',
              description: 'Invoice has been deleted',
            })

            const data = this.usersData
            const query = id
            // search through the array and get the index array with the passed value
            const index = data.map(function (img) {
              return img.id
            }).indexOf(query)
            // filter through the data, remove the value we don't want (with index) and return the values we need
            const filteredItems = data.slice(0, index).concat(data.slice(index + 1, data.length))
            // replace the data with the returned value
            this.usersData = filteredItems
          }
        })
        .catch(error => {
          console.log(error)
        })
    },
    getInvoice() {
      this.axios.get('invoice/all')
        .then(response => {
          // console.log('clients', response.data.data)
          const dt = response.data.data.invoices
          const data = []
          for (let i = 0; i < dt.length; i++) {
            const userData = {
              customer_name: {
                name: dt[i].invoice_shipping_mark,
                id: dt[i].invoice_id,
              },
              address: dt[i].client_address,
              business_name: dt[i].client_business_name,
              phone: dt[i].invoice_shipment_phone,
              email: dt[i].client_email,
              shipping_phone: dt[i].invoice_shipment_phone,
              shipping_ctn: dt[i].invoice_shipment_ctn,
              shipping_cbm: dt[i].invoice_shipment_cbn,
              total_usd: dt[i].invoice_shipment_total_usd,
              total_ngn: dt[i].invoice_shipment_total_naira,
              container: dt[i].invoice_shipment_container_no,
              id: dt[i].invoice_id,
              key: dt[i].invoice_id,
              action: {
                0: {
                  id: 'view',
                  name: 'View Invoice',
                  url: dt[i].invoice_id,
                },
                1: {
                  id: 'delete',
                  name: 'Delete',
                  url: dt[i].invoice_id,
                },
              },
            }
            data.push(userData)
          }
          this.usersData = data
          // console.log('user data ', this.usersData)
        })
        .catch(error => {
          this.$notification['warning']({
            message: 'Invoice Data',
            description: 'Sorry an error occurred',
          })
          return error
        })
    },
  },
}
</script>
